/* global console, document, Excel, Office */

// The initialize function must be run each time a new page is loaded
Office.onReady(() => {
  document.getElementById("sideload-msg").style.display = "none";
  document.getElementById("app-body").style.display = "flex";
  document.getElementById("run").onclick = fillTableContentNoThrow;
  document.getElementById("auto-open").onclick = setAutoOpenOnNoThrow;
});

export async function fillTableContentNoThrow() {
  try {
    await fillInTableContent();
  } catch (error) {
    console.error(error);
  }
}

export async function setAutoOpenOnNoThrow() {
  try {
    await setAutoOpenOn();
    console.log("Operation Success");
  } catch (error) {
    console.error(error);
  }
}

export async function setAutoOpenOn() {
  return Excel.run(async function (context) {
    Office.context.document.settings.set("Office.AutoShowTaskpaneWithDocument", true);
    Office.context.document.settings.saveAsync();

    await context.sync();
  });
}

